import Experience from "../Experience.js"
import QRCode from "qrcode"

export default class WebRTC {
    constructor() {
        this.experience = new Experience();
        
        //   handshakeId  ( )
        this.handshakeIds = {
            left: this.uuid(),
            right: this.uuid()
        };
        
        //     
        this.data = {
            left: {
                orientation: {
                    alpha: 63.0,
                    beta: 120.0,
                    gamma: -22.0
                },
                motion: {
                    x: 0.0,
                    y: 0.0,
                    z: 0.0,
                    interval: 0.0
                }
            },
            right: {
                orientation: {
                    alpha: 63.0,
                    beta: 120.0,
                    gamma: -22.0
                },
                motion: {
                    x: 0.0,
                    y: 0.0,
                    z: 0.0,
                    interval: 0.0
                }
            }
        };
        
        //      
        this.connections = {
            left: {
                answer: undefined,
                ready: undefined,
                dc: null,
                pc: null
            },
            right: {
                answer: undefined,
                ready: undefined,
                dc: null,
                pc: null
            }
        };

        this.config = {
            iceServers: [{
                urls: "stun:stun.l.google.com:19302"
            }]
        };

        this.initWebSocket();
    }

    uuid = () => {
        let dt = new Date().getTime();
        const uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
            const r = (dt + Math.random() * 16) % 16 | 0;
            dt = Math.floor(dt / 16);
            return (c == 'x' ? r : (r & 0x3 | 0x8)).toString(16);
        });
        return uuid;
    }

    generateQR = async (url, targetElement) => {
        try {
            const imageQR = await QRCode.toDataURL(url);
            document.getElementById(targetElement).src = imageQR;
        } catch (err) {
            console.error(err);
        }
    }

    handleDeviceOrientation = (event, side) => {
        const alpha = event.alpha;
        const beta = event.beta;
        const gamma = event.gamma;

        // JSON  
        const data = {
            orientation: {
                alpha,
                beta,
                gamma
            },
            motion: {
                x: 0.0,
                y: 0.0,
                z: 0.0
            },
            side: side //   
        };

        // JSON   
        const string = JSON.stringify(data);

        if (this.connections[side].dc && this.connections[side].dc.readyState === 'open') {
            this.connections[side].dc.send(string);
        }
    }

    requestAccessToDeviceSensors(side) {
        if (typeof DeviceOrientationEvent !== 'undefined' && typeof DeviceOrientationEvent.requestPermission === 'function') {
            DeviceOrientationEvent.requestPermission()
                .then(permissionState => {
                    if (permissionState === 'granted') {
                        //     
                        const handler = (event) => this.handleDeviceOrientation(event, side);
                        window.addEventListener('deviceorientation', handler, true);
                    } else {
                        console.error('Access to orientation not granted');
                    }
                })
                .catch(console.error);
        } else {
            const handler = (event) => this.handleDeviceOrientation(event, side);
            window.addEventListener('deviceorientation', handler, true);
        }
    }

    async initWebSocket() {
        const ws = new WebSocket(`wss://${__SOCKET_HOST__}`);
        
        // URL id side  
        const urlParams = new URLSearchParams(window.location.search);
        const handshakeId = urlParams.get("id");
        const side = urlParams.get("side");

        ws.onopen = () => {
            console.log('Connected to the signaling server');

            if (handshakeId && side) {
                //  :  
                ws.send(JSON.stringify({
                    type: 'JOIN_SESSION',
                    handshakeId,
                    side
                }));
                
                //      
                this.initPeerConnection(ws, side, handshakeId);
            } else {
                // :    
                //   QR  
                this.generateQR(`${window.location.origin}/?id=${this.handshakeIds.left}&side=left`, 'qrcode-left').then(() => {
                    this.experience.html.qrcodeLeft.style.display = 'block';
                });
                
                //   QR  
                this.generateQR(`${window.location.origin}/?id=${this.handshakeIds.right}&side=right`, 'qrcode-right').then(() => {
                    this.experience.html.qrcodeRight.style.display = 'block';
                });

                //     
                ws.send(JSON.stringify({
                    type: 'CREATE_SESSION',
                    handshakeId: this.handshakeIds.left,
                    side: 'left'
                }));
                
                ws.send(JSON.stringify({
                    type: 'CREATE_SESSION',
                    handshakeId: this.handshakeIds.right,
                    side: 'right'
                }));
                
                //   
                this.initPeerConnection(ws, 'left', this.handshakeIds.left);
                this.initPeerConnection(ws, 'right', this.handshakeIds.right);
            }
        };

        ws.onmessage = async (message) => {
            const data = JSON.parse(message.data);
            const targetSide = data.side || (side || 'left');  //  left

            switch (data.type) {
                case 'SESSION_CREATED':
                    console.log(`Session created for ${targetSide} with ID: ${data.handshakeId}`);
                    break;
                case 'SESSION_JOINED':
                    console.log(`Device joined the ${targetSide} session: ${data.handshakeId}`);
                    
                    //   
                    if (!this.experience.isMobile) {
                        this.connections[targetSide].pc.createOffer().then(offer => {
                            this.connections[targetSide].pc.setLocalDescription(offer).then(() => {
                                ws.send(JSON.stringify({
                                    type: 'OFFER',
                                    handshakeId: data.handshakeId,
                                    side: targetSide,
                                    sdp: this.connections[targetSide].pc.localDescription.sdp
                                }));
                            });
                        });
                    }
                    break;
                case 'OFFER':
                    if (side) {  //  
                        await this.connections[side].pc.setRemoteDescription({ type: 'offer', sdp: data.sdp });
                        const answer = await this.connections[side].pc.createAnswer();
                        await this.connections[side].pc.setLocalDescription(answer);
                        
                        ws.send(JSON.stringify({
                            type: 'ANSWER',
                            handshakeId: handshakeId,
                            side: side,
                            sdp: answer.sdp
                        }));
                    }
                    break;
                case 'ANSWER':
                    if (!this.experience.isMobile) {
                        this.connections[targetSide].pc.setRemoteDescription({ type: 'answer', sdp: data.sdp });
                    }
                    break;
                case 'ICE_CANDIDATE':
                    const targetConnection = this.connections[targetSide];
                    if (targetConnection && targetConnection.pc) {
                        try {
                            await targetConnection.pc.addIceCandidate(data.candidate);
                        } catch (e) {
                            console.error("Error adding ice candidate", e);
                        }
                    }
                    break;
            }
        };
    }

    initPeerConnection(ws, side, handshakeId) {
        // RTCPeerConnection    
        const pc = new RTCPeerConnection(this.config);
        const dc = pc.createDataChannel("chat", {
            negotiated: true,
            id: 0
        });
        
        //   
        this.connections[side].pc = pc;
        this.connections[side].dc = dc;
        
        //    
        dc.onopen = () => {
            console.log(`Data channel for ${side} opened`);
        };
        dc.onclose = () => console.log(`Data channel for ${side} closed`);
        dc.onerror = err => console.log(`Error in ${side} data channel:`, err);
        
        //   
        dc.onmessage = e => {
            let data = JSON.parse(e.data);
            
            if (data.orientation) {
                this.data[side].orientation = data.orientation;
            }
            
            if (data.motion) {
                this.data[side].motion = data.motion;
            }
            
            //    
            if (data.saberEnable) {
                this.experience.world.saber.activateSaber(side);
            }
        };
        
        // ICE  
        pc.onicecandidate = event => {
            if (event.candidate) {
                ws.send(JSON.stringify({
                    type: 'ICE_CANDIDATE',
                    handshakeId: handshakeId,
                    side: side,
                    candidate: event.candidate
                }));
            }
        };
        
        //    
        pc.onconnectionstatechange = () => {
            console.log(`${side} connection state:`, pc.connectionState);
        };
        
        pc.oniceconnectionstatechange = () => {
            console.log(`${side} ICE connection state:`, pc.iceConnectionState);
        };
        
        //   ,       
        if (this.experience.isMobile) {
            const urlParams = new URLSearchParams(window.location.search);
            const mobileSide = urlParams.get("side");
            
            if (mobileSide === side) {
                document.getElementById('permissions').addEventListener('click', () => {
                    this.requestAccessToDeviceSensors(side);
                });
                
                document.getElementById('calibrate').addEventListener('click', () => {
                    if (typeof DeviceOrientationEvent !== 'undefined') {
                        const handler = (event) => this.handleDeviceOrientation(event, side);
                        window.removeEventListener('deviceorientation', handler, true);
                        window.addEventListener('deviceorientation', handler, true);
                    }
                });
            }
        }
        
        return { pc, dc };
    }
}
