import * as THREE from 'three'
import Experience from '../Experience.js'

export default class Saber {
    constructor() {
        this.experience = new Experience()
        this.debug = this.experience.debug
        this.scene = this.experience.scene
        this.time = this.experience.time
        this.camera = this.experience.camera
        this.renderer = this.experience.renderer.instance
        this.resources = this.experience.resources
        this.sound = this.experience.sound
        this.webRTC = this.experience.webrtc

        //       
        this.sabers = {
            left: {
                model: null,
                group: null,
                animation: null,
                active: false
            },
            right: {
                model: null,
                group: null,
                animation: null,
                active: false
            }
        };

        this.createModels();
        this.setAnimations();
        this.setActions();
    }

    setActions() {
        if (this.experience.isMobile) {
            //   (/)   
            const side = new URLSearchParams(window.location.search).get("side") || 'left';
            
            document.getElementById('saber').addEventListener('click', () => {
                if (this.experience.webrtc.connections[side].dc && 
                    this.experience.webrtc.connections[side].dc.readyState === 'open') {
                    const saberEnableMessage = {
                        saberEnable: true,
                        side: side
                    };

                    this.experience.webrtc.connections[side].dc.send(JSON.stringify(saberEnableMessage));
                }
            });
        } else {
            //  /    
            document.getElementById('saber-left').addEventListener('click', () => {
                this.activateSaber('left');
            });
            
            document.getElementById('saber-right').addEventListener('click', () => {
                this.activateSaber('right');
            });
        }
    }

    activateSaber(side) {
        if (this.sabers[side] && this.sabers[side].animation && this.sabers[side].animation.actions) {
            this.sabers[side].animation.actions.open.play();
            this.sabers[side].active = true;
            
            //   (      )
            if (this.sound.saberOpenSound) {
                this.sound.saberOpenSound.play();
            }
            
            if (this.sound.saberIdleSound && !this.sound.saberIdleSound.isPlaying) {
                this.sound.saberIdleSound.play();
            }
        }
    }

    createModels() {
        this.resource = this.experience.resources.items.lightSaberModel;

        //   
        this.sabers.left.group = new THREE.Group();
        this.sabers.left.model = this.resource.scene.clone();
        this.sabers.left.model.rotateY(-Math.PI / 2);
        this.sabers.left.group.add(this.sabers.left.model);
        this.sabers.left.group.position.set(-0.5, 0, 0); //  
        this.scene.add(this.sabers.left.group);

        //   
        this.sabers.right.group = new THREE.Group();
        this.sabers.right.model = this.resource.scene.clone();
        this.sabers.right.model.rotateY(-Math.PI / 2);
        this.sabers.right.group.add(this.sabers.right.model);
        this.sabers.right.group.position.set(0.5, 0, 0); //  
        this.scene.add(this.sabers.right.group);
    }

    setAnimations() {
        //   
        this.sabers.left.animation = {};
        this.sabers.left.animation.mixer = new THREE.AnimationMixer(this.sabers.left.model);
        this.sabers.left.animation.actions = {};
        this.sabers.left.animation.actions.idle = this.sabers.left.animation.mixer.clipAction(this.resource.animations[11]);
        this.sabers.left.animation.actions.open = this.sabers.left.animation.mixer.clipAction(this.resource.animations[5]);
        this.sabers.left.animation.actions.current = this.sabers.left.animation.actions.idle;
        this.sabers.left.animation.actions.current.play();
        this.sabers.left.animation.actions.open.loop = THREE.LoopOnce;
        this.sabers.left.animation.actions.open.clampWhenFinished = true;

        //   
        this.sabers.right.animation = {};
        this.sabers.right.animation.mixer = new THREE.AnimationMixer(this.sabers.right.model);
        this.sabers.right.animation.actions = {};
        this.sabers.right.animation.actions.idle = this.sabers.right.animation.mixer.clipAction(this.resource.animations[11]);
        this.sabers.right.animation.actions.open = this.sabers.right.animation.mixer.clipAction(this.resource.animations[5]);
        this.sabers.right.animation.actions.current = this.sabers.right.animation.actions.idle;
        this.sabers.right.animation.actions.current.play();
        this.sabers.right.animation.actions.open.loop = THREE.LoopOnce;
        this.sabers.right.animation.actions.open.clampWhenFinished = true;

        //    
        const setPlayMethod = (side) => {
            this.sabers[side].animation.play = (name) => {
                const newAction = this.sabers[side].animation.actions[name];
                const oldAction = this.sabers[side].animation.actions.current;

                newAction.reset();
                newAction.play();
                newAction.crossFadeFrom(oldAction, 1);

                this.sabers[side].animation.actions.current = newAction;
            };
        };

        setPlayMethod('left');
        setPlayMethod('right');
    }

    update() {
        //  
        if (this.sabers.left.animation) {
            this.sabers.left.animation.mixer.update(this.time.delta);
        }
        
        if (this.sabers.right.animation) {
            this.sabers.right.animation.mixer.update(this.time.delta);
        }

        //   quaternion 
        if (this.webRTC.data.left) {
            var alpha = THREE.MathUtils.degToRad(this.webRTC.data.left.orientation.alpha);
            var beta = THREE.MathUtils.degToRad(this.webRTC.data.left.orientation.beta);
            var gamma = THREE.MathUtils.degToRad(this.webRTC.data.left.orientation.gamma);

            var quaternion = new THREE.Quaternion();
            var euler = new THREE.Euler();

            euler.set(beta, alpha, -gamma, 'YXZ');
            quaternion.setFromEuler(euler);

            this.sabers.left.group.quaternion.copy(quaternion);
        }

        //   quaternion 
        if (this.webRTC.data.right) {
            var alpha = THREE.MathUtils.degToRad(this.webRTC.data.right.orientation.alpha);
            var beta = THREE.MathUtils.degToRad(this.webRTC.data.right.orientation.beta);
            var gamma = THREE.MathUtils.degToRad(this.webRTC.data.right.orientation.gamma);

            var quaternion = new THREE.Quaternion();
            var euler = new THREE.Euler();

            euler.set(beta, alpha, -gamma, 'YXZ');
            quaternion.setFromEuler(euler);

            this.sabers.right.group.quaternion.copy(quaternion);
        }
    }
}
